.App {
  margin-left: auto;
  margin-right: auto;
}

.LoginForm {
  width: 450px;
  margin-left: auto;
  margin-right: auto;
  padding: 60px;
}

.carouselSlides {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.homeTable {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  text-align: right;
}

.homeTable td {
  padding: 10px;
  font-size: 1.5em;
}

#donation {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.donationTable {
  margin-left: auto;
  margin-right: auto;
}

.donationTable td {
  padding: 5px;
}

.donationTable .card {
  width: 18rem;
}

.DonateMoney .content, .DonateFood .content, .Confirmation .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto; 
  min-height: 50em;
}

.paymentSection table {
  margin-left: auto;
  margin-right: auto; 
}

.paymentSection table td {
  padding-right: 10px;
}

.paymentSection {
  width: 60%;
  margin-left: auto;
  margin-right: auto; 
}

.productList {
  width: 100%;
  margin-left: auto;
  margin-right: auto; 
}
.productList .card {
  width: 15em;
  margin: 20px;
  padding: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.productList .card p, .productList .card img {
  width: 100%;
}
.productList .card img {
  height: 10em;
}
.productList .card button {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1em;
}

.MyCart .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 50em;
}

.MyCart li {
  max-width: 40em;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
}

#alertNotification {
  position: fixed;
  top: 60px;
  z-index: 10;
  min-width: 80%;
}

/* Mobile css */
.Mobile .carouselSlides {
  width: 100%;
}
.Mobile h3 {
  font-size: 5vw;
}
.Mobile .carouselSlides h3 {
  font-size: 4vw;
}
.Mobile .carouselSlides p {
  font-size: 3vw;
}
.Mobile .homeTable {
  width: 100%;
}
.Mobile .homeTable td {
  width: 50%;
}
.Mobile .homeTable p {
  font-size: 4vw;  
}
.Mobile .homeTable img {
  width: 100%;
}
.Mobile .donationTable td, .Mobile .productList td {
  float: left;  
}
.Mobile .donationTable .card, .Mobile .productList .card {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.Mobile .donationTable .card img, .Mobile .productList .card img {
  width: 100%;
}
.Mobile .paymentSection {
  width: 95%;
}

.Admin {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.addToOrder, .removeFromOrder {
  border: 1px solid grey; /* Green */
  padding: 0;
  padding: 2px 7px;
  margin-right: 10px;
  margin-left: 10px;
}

#myItems, #deliveryDetails {
  width: 50em;
  margin-left: auto;
  margin-right: auto;
}

.recipient {
  width: 50%;
  min-width: 50em;
  margin-left: auto;
  margin-right: auto;
}
#deliveryDetails td {
  padding: 10px;
}
.orderFilter {
  display: inline-block;
}
.orderFilter #formInput {
  width: 10em;
}

#filteredOrder, .pendingOrders .order, .completedOrders .order {
  border: 1px solid grey;
  width: 50%;
  min-width: 50em;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
}
